.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.menu {
  margin: 20px 0;
}

.menu button {
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.menu button:hover {
  background-color: #0056b3;
}

.content {
  margin-top: 20px;
}

.form-container, .search-container, .search-by-name-container, .upload-container {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.subject-input {
  margin: 10px 0;
}

.subject-input label {
  display: block;
  margin-bottom: 5px;
}

.results .student-info {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
}

.results .student-info p {
  margin: 5px 0;
}

input[type="text"], select {
  padding: 8px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  width: calc(100% - 20px);
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #218838;
}

button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}
